.video-blog {
  position: relative;

  &__aside {
    height: auto;

    .mCSB_container {
      overflow: visible;
    }
  }

  @include media('>md') {
    &__aside {
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      padding-left: 5px;

      .mCSB_container {
        margin-right: 10px;
        overflow: hidden;
      }

      .mCSB_scrollTools {
        width: 5px;
      }
    }
  }

  @include media('<md') {
    &__video {
      margin-bottom: 30/16 + rem;
    }

    .article-video {
      margin-bottom: 24/16 + rem;
    }

    .mCSB_container {
      margin-right: 0 !important;
    }

    .mCSB_scrollTools {
      display: none !important;
    }
  }
}

.article-video {
  margin-bottom: 30/16 + rem;

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    font-family: inherit;
  }

  &__date {
    font-size: 12px;
    color: #6b6a6a;
  }

  &__view {
    font-size: 13px;
    color: #6b6a6a;
  }

  &__thumb {
    position: relative;
    min-height: 80px;
    margin-bottom: 0;
  }

  &__play {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
  }
}
