/**
 * Modals Default
 */
.modal-popup,
.modal-product {
  font-size: 1rem;

  .modal-dialog {
    max-width: 800px;
  }

  .modal-content {
    border-radius: 4px;
    width: 100%;
  }

  h4, .h4 {
    font-size: 18/16 + em;
    line-height: 1.1;
    color: $gray-base-100;
  }

  h5 {
    font-size: 14/16 + rem;
  }

  h6 {
    font-size: 12px;
  }

  .btn {
    padding: 8/16 + em 1em;
    font-size: 1em;
  }

  .modal-header {
    padding: 0;
    background-color: $gray-lighter-2;
  }

  .modal-title {
    flex-grow: 1;
    padding: 10/16 + em 15px;
  }

  .icon-status {
    width: 30/16 + em;
    flex-shrink: 0;
    margin-right: 12/16 + em;
  }

  .close {
    flex-shrink: 0;
    position: relative;
    width: 48/16 + em;
    padding: 15/16 + em;
    height: auto;
    margin: 0;
    font-size: 1em;
    color: $gray-dark;
    box-sizing: border-box;
    border-top-right-radius: 4px;
    transition: 200ms;
    outline: 1px solid;

    &:before, &:after {
      display: block;
      content: '';
      width: 2px;
      height: 18/16 + em;
      margin-left: 8/16 + em;
      background-color: currentColor;
    }

    &:before {
      transform: rotate(45deg);
    }

    &:after {
      margin-top: -18/16 + em;
      transform: rotate(-45deg);
    }

    &:hover {
      background-color: lighten($gray-lighter-2, 2%);
    }
  }

  .modal-body {
    padding: 40/16 + em 1em 1em;
  }

  @include media('>md') {
    .modal-body {
      padding: 40/16 + em 20/16 + em 20/16 + em;
    }
  }

  @include media('<sm') {
    font-size: 0.8rem;
  }
}

/**
 * Modals centered
 */
 .modal-dialog-centered {
  display: flex;
  align-items: center;
  min-height: calc(100% - (.5rem * 2));
  margin: .5rem;

  @include media('>sm') {
    min-height: calc(100% - (1.75rem * 2));
    margin: 1.75rem auto;
  }
}

/**
 * Show modal after add to cart
 */
.modal-product {
  .product {
    &__img {
      height: 140px;
    }
  }

  .slider-arrow {
    &.swiper-button-prev {
      left: 2/16 + em;
    }
    &.swiper-button-next {
      right: 2/16 + em;
    }
  }

  @include media('>md') {
    .wrapper-slider-items {
      margin: 0 -14/16 + em;
    }

    .items-row {
      &__item {
        padding-bottom: 4px;
      }
    }
  }
}
