/**************************************
      = Base
                                     */
html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

body {
  font-family: $font-family-base;
  color: #333333;
  background-color: #fff;
}

// Style anchor elements.
$link-color:            $teal;
$link-hover-color:      darken($link-color, 7%);

a {
  color: $link-color;
  text-decoration: none;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;

  &:hover,
  &:focus {
    color: $link-hover-color;
    text-decoration: underline;
  }

  &.under {
    text-decoration: underline;

    &:focus {
      text-decoration: underline;
    }

    &:hover {
      text-decoration: none;
    }
  }
}

.link-category {
  font-size: 12px;
  line-height: 1;
  color: #909090 !important;
}

$headings-font-family:    inherit;
$headings-font-weight:    500;
$headings-line-height:    1.1;
$headings-color:          inherit;

$font-size-h1: 24/16 + rem;
$font-size-h2: 18px;
$font-size-h3: 18px;
$font-size-h4: 18px;
$font-size-h5: 12px;
$font-size-h6: 12px;

h1 { font-size: $font-size-h1; color: #141414;};
h2 {
  font-weight: 600;
  margin-top: 10/16 + rem;
  margin-bottom: 24/16 + rem;
  font-size: $font-size-h2;
}
h3 { font-size: $font-size-h3; }
h4 { font-size: $font-size-h4; font-weight: 600; margin-bottom: 15px;}
h5 { font-size: $font-size-h5; font-weight: 700; margin-bottom: 15px;}
h6 { font-size: $font-size-h6; }

.page-title {
  h1 {
    margin: 20/16 + rem 0;
    font-weight: 400;
    font-size: 36/16 + rem;
    line-height: 1.2;
  }

  &__count {
    font-weight: 300;
    color: #797979;
  }

  &.offset {
    h1 {
      margin: 0 0 10px;
      font-size: 30/16 + rem;
    }
  }
}

@include media('<md') {
  .page-title,
  .page-title.offset {
    padding: 20px 15px;
    margin: 0 -15px;

    h1 {
      margin: 0;
      font-size: 24/16 + rem;
    }
  }

  h2 {
    margin: 13/16 + rem 0;
  }
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

p {
  margin-bottom: 20px;

  &.small {
    font-size: 14px;
  }

  &.large {
    font-weight: 500;
    font-size: 20/16 + rem;
  }
}

.text-large {
  font-size: 18/16 + rem;
}

.text-success {
  color: $brand-success;
}

.text-danger {
  color: $brand-danger;
}

.text-warning {
  color: $brand-warning;
}

.bg-light {
  background-color: $bg-light;
}

.cl-md {
  @include cf();
}

.form-group.has-error {
  .form-control {
    border-color: $brand-danger;
    box-shadow: 0 0 0 0.2rem rgba(220,53,69,.25) !important;
  }

  .invalid-feedback {
    display: block;
  }
}

.section-description {
  font-size: 14px;

  h4 {
    font-size: 14px;
  }
}

.bg-over {
  background: $gray-lightest url() no-repeat center / cover;
}

.img-fake {
  opacity: 0;
}

@include media('>md') {
  .md-none {
    position: absolute;
    opacity: 0;
    z-index: -2;
  }
}

.col-fixed {
  flex-grow: 0;
  flex-shrink: 0;
}

textarea.form-control {
  resize: none;
  width: 100%;
}

.img-cover {
  position: absolute;
  object-fit: cover;
  /* support for plugin https://github.com/bfred-it/object-fit-images */
  font-family: 'object-fit: cover;';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}


.xzoom-lens img {
  opacity: 0;
}

