.review-opinion {
  max-width: 240px;
  font-size: 13px;

  * {
    vertical-align: middle
  }

  &__title {
    white-space: nowrap;
    margin-right: 15px;
    font-weight: 400;
    font-size: 14px;
  }

  &__list {
    > * {
      margin: 2px 0;
      white-space: nowrap;
    }
  }

  &__btn.btn {
    min-width: 28px;
    padding: 4px 2px;
    margin: 0 5px;
    font-size: 10px;
    line-height: 1;
    text-transform: uppercase;
  }
}

.stars-rating {
  margin: 30/16 + rem 0;
  white-space: nowrap;

  &__title {
    margin: 0 0 28/18 + em;
    font-size: 18px;
    font-weight: 600;
    line-height: 34/18 + em;
    color: #262626;
  }

  &__percentage {
    font-size: 14px;
    color: #6d6d6d;
  }

  &__review {
    font-size: 13px;
  }
}

.comments-posts {
  &__posts {
    flex-grow: 1;
  }

  &__stars {
    min-width: 270px;
  }
}

.comment-post {
  font-size: 14px;
  margin-bottom: 30/16 + rem;

  &:nth-of-type(n+3) {
    margin-top: 50/16 + rem;
  }

  .comment-post {
    margin: 40/16 + rem 0 0;
  }

  &__thumb {
    display: block;
    width: 100%;
    max-width: 30px;
    margin: 2px 18/16 + rem 0 0;
    border-radius: 3px;
    overflow: hidden;
  }

  &__navbar {
    font-size: 13px;
  }

  &__name {
    color: #418726;
  }

  &__date {
    color: #919191;
  }

  &__rating {
    .title {
      white-space: nowrap;
      margin: 5px 5px 5px 0;
      color: #2b2b2b;
    }

    .widget-rating {
      width: auto !important;

      svg {
        margin-right: 3px;
      }
    }
  }

  &__message {
    margin-bottom: 20/16 + rem;
    line-height: 22/16 + rem;
    color: #696969;

    &:last-of-type() {
      margin-bottom: 0;
    }

    * {
      margin: 0;
      font-family: inherit;
      font-size: inherit;
    }

    h1, h2, h3, h4, h5, h6 {
      font-weight: 700;
      color: #3b3b3b;
    }
  }

  &__status {
    &.success {
      color: #708043;
    }

    &.answer {
      color: #b56506;
    }
  }

  @include media('<md') {
    display: block !important;

    &:nth-child(n+3) {
      margin-top: 34/16 + rem;
    }

    &__thumb {
      display: block !important;
      float: left;
    }
    &__navbar {
      display: block !important;
    }

    .stars-rating {
      margin: 20/16 + rem 0 !important;
    }
  }
}
