.btn {
  padding: 9px;
  font-family: $roboto;
  box-shadow: none !important;

  &-light {
    background-color: #e5e4e4;
    color: #2b2b2b;
  }

  &-primary {
    background-color: $brand-primary;
    border-color: $brand-primary;

    &:hover {
      background-color: $brand-primary;
      border-color: $brand-primary;
    }
  }

  &-secondary {
    &.input-group-addon {
      color: inherit !important;
      box-shadow: none !important;

      &:hover {
        background-color: darken(#e9ecef, 3%) !important;
        border-color: darken(#e9ecef, 3%) !important;
      }

      &.is-active {
        color: $brand-success !important;
      }
    }
  }

  &-warning,
  &-third {
    background-color: $brand-warning;
    color: #ffffff !important;

    &:hover {
      background-color: $brand-warning;
    }

    &::active {
      background-color: ligten($brand-warning, 5%);
    }
  }

  &--xs {
    border-radius: 2px;
    padding: 8px 13px;
    border-radius: 3px;
    font-weight: 500;
    font-family: $roboto;
    font-size: 14px;
    line-height: 1;
    text-transform: uppercase;
  }

  &--responsive {
    white-space: normal;
  }
}

.btn-more-wrap {
  margin-bottom: 20px;
}

.btn-more {
  font-weight: 400;
  font-size: 16px;
  padding: 13px;
  text-transform: none;
  background-color: transparent;
  border: 1px solid #cccccc !important;
  border-radius: 0;
  color: #707070;

  &:active {
    background-color: #cccccc !important;
  }

  &:hover {
    background-color: #f4f4f4;
    color: #707070;
  }
}
