.menu-category {
  position: relative;
  flex-shrink: 0;

  &--menu {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;

    .menu-category__list {
      padding: 10px 10px 15px 20px;
      background-color: $breadcrumb-bg;
    }
  }

  &__list {
    width: 260px;
    padding: 10px 15px 0 0;
    background-color: #ffffff;
  }

  &__item {
    &.toggle-list {
      padding-left: 50px;

      ~ * {
        display: none;
      }
    }
  }

  &__item-icon {
    width: 50px;
    padding-right: 10px;
    overflow: hidden;
    text-align: center;

    &:before {
      display: inline-block
    }
  }

  &__link {
    display: table;
    width: 100%;
    padding: 4px 0;
    font-size: 14px;
    color: $gray-base;

    > * {
      display: table-cell;
      vertical-align: middle;
    }

    &:focus,
    &:hover {
      color: $gray-base;
    }
  }

  &__submenu {
    display: none;
    padding-left: 50px;
  }
}
