// Start with assigning color names to specific hex values.
$white:  #fff;
$black:  #000;
$red:    #c63939;
$red-dark: darken($red, 10%);
$red-lighten: lighten($red, 12%);
$orange: #d97c3e;
$yellow: #eddd0f;
$green:  #3aa803;
$blue:   #5284c8;
$teal:   #5f96ce;
$teal-light: #c6edff;
$pink:   #ff5b77;
$purple: #613d7c;

// Create grayscale
$gray-base:                 #000101;
$gray-base-100:             #111111;
$gray-dark-1:               #242424;
$gray-dark:                 #262626;
$gray:                      #5a5a5a;
$gray-light:                #636c72;
$gray-lighten:              #959595;
$gray-lighter:              #c7c7c7;
$gray-lighter-2:            #e7e7e7;
$gray-lighter-300:          #f0f0f0;
$gray-lightest:             #f7f7f9;
$gray-lightest-ease:        #f9f9f9;

// Reassign color vars to semantic color scheme
$brand-primary:             $blue;
$brand-success:             $green;
$brand-info:                $teal;
$brand-warning:             $orange;
$brand-danger:              $red;
$brand-inverse:             $gray-dark;

$font-family-base: -apple-system, BlinkMacSystemFont, 'Open Sans', 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
$roboto: 'Roboto', sans-serif;

$grid-gutter: 10px;
$grid-ml-offset: 20px;

$dropdown-menu-bg: #e3e3e3;

$bg-light: #f4f4f4;

$btn-youtube-play: #c30000;
