.product-mod {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: $item-bg;
  box-shadow: 0 0 8px rgba(#000101, 0.19);

  &__img {
    margin: 0;
    overflow: hidden;
    max-height: 180px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: $gray-lighten url() no-repeat center / cover;

    img {
      width: 100%;
      opacity: 0;
    }
  }

  &__description {
    padding: 10px 10px 0;
    flex-grow: 1;
  }

  &__title {
    margin: 0;
    font-size: 16px;
    font-weight: 400;

    + .product__category {
      margin-bottom: 0;
    }
  }

  &__category {
    margin: 4px 0;
    font-size: 12px;
    line-height: 18px;
    color: #909090;

    a {
      color: inherit;
    }
  }

  &__cost {
    font-size: 18px;
    margin-bottom: 10px;
    line-height: 1;
    color: #242424;
    white-space: nowrap;

    .bold {
      display: inline-block;
      vertical-align: bottom;
      font-size: 18px;
      line-height: 1;
      font-weight: 700;
    }
  }

  &__bottom {
    margin-top: 10px;

    .btn-cart {
      padding: 4px 7px;

      .icon {
        font-size: 20px;
      }
    }
  }

  &__bottom-right {
    max-width: 135px;
    margin: 0 auto;

    > * {
      margin-top: 7px;
    }

    .btn-cart {
      font-size: 14px;
    }
  }

  @include media('>md') {
    &__description {
      padding-bottom: 10px;
    }

    &__cost {
      margin-bottom: 0;
    }

    &__bottom-right {
      margin-right: 0;

      > * {
        margin: 0 0 0 10px;
      }

      .btn-cart {
        font-size: 18px;
      }
    }
  }
}
