.callback-popover {
  max-width: none;
  border: none;

  .arrow {
    z-index: 15;
  }

  .popover-body {
    padding: 0;
  }
}

.callback-panel {
  border-radius: 6px;
  min-width: 340px;

  > * {
    padding: 13px 20px;
  }

  .modal-header,
  .modal-footer {
    position: relative;
    z-index: 10;
    border: none;
  }

  .modal-header {
    background-color: darken($gray-lightest-ease, 1%);
    box-shadow: 0 1px 1px rgba(#000, .15);

    .close {
      position: absolute;
      top: 0;
      right: 0;
      padding: 12px;
      margin: 0;
    }
  }
  .modal-title {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    width: 100%;
    color: #555;
    font-weight: 600;
    text-align: center;
  }

  .modal-body {
    background-color: $gray-lightest-ease;
    height: 350px;
    overflow: hidden;
    padding: 0;

    .mCSB_container {
      margin: 0 15px;
    }

    .mCSB_scrollTools .mCSB_draggerContainer {
      top: 10px;
      bottom: 10px;
    }

    .mCS-dark.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
      width: 3px;
      border-radius: 0;
      background-color: rgba($gray-lighter, 0.90);
    }

    .mCSB_scrollTools .mCSB_draggerRail {
      width: 3px;
      border-radius: 0;
    }

    &__content {
      padding: 10px 5px;
    }
  }

  .modal-footer {
    box-shadow: 0 -1px 1px rgba(#000, .15);
  }

  span.label {
    display: block;
    margin-bottom: 5px;
  }

  .form-control {
    padding: 6px;
    border-radius: 4px;
    font-size: 14px;
  }

  textarea.form-control {
    resize: none;
    width: 100%;
  }

  .custom-file {
    height: auto;
    width: 100%;

    .custom-file-input {
      height: auto;
      padding: 14px;
    }

    .custom-file-control {
      height: auto;
      padding: 15px;
      border: 2px dashed #ccc;
      box-shadow: none !important;
      pointer-events: auto;

      &:before {
        content: '';
        display: none;
      }

      .icon {
        vertical-align: middle;
        margin-right: 5px;
      }

      &:hover {
        background-color: $gray-lightest-ease !important;
      }
    }
  }
}
