.navbar-tabs {
  flex-wrap: nowrap;
  position: relative;
  margin-bottom: 30/16 + rem;
  box-shadow: 0 0 5px 0 rgba(#000, 0.25);
  text-align: center;
  border-bottom: 1px solid darken($gray-lightest, 5%);
  overflow: hidden;
  z-index: 1;

  .nav-item {
    display: flex;
    flex-grow: 1;
    margin-left: -1px;
  }

  .nav-link {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    min-height: 90px;
    padding: 8px;
    font-size: 14px;
    color: #141414;
    background-color: $gray-lightest;
    border: 1px solid #cecece;
    border-width: 1px 0 0 1px;
    transition: 120ms ease;
    border-radius: 0;
    box-shadow: inset 0 -3px 0 0 transparent;

    &.active {
      border-color: #cecece !important;
      background-color: #fff;
      box-shadow: inset 0 -3px 0 0 $brand-warning;
    }

    &__badge {
      flex-shrink: 0;
      font-weight: 100;
      font-family: $roboto;
      position: relative;
      font-size: 9px;
      line-height: 1;
      min-width: 17px;
      height: 17/9 + em;
      padding: 4/9 + em 3px;
      margin: -10px 0 0 4px;
      text-align: center;
      border-radius: 17/2/9 + em;
    }
  }

  &--catalog {
    box-shadow: 0 0 3px 0 rgba(#000, 0.25);

    .nav-link {
      min-height: 60px;
      padding: 5px;
      font-weight: 500;
      font-size: 16px;
      color: $black;
      background-color: #f5f5f5;

      .count {
        margin-left: 4px;
        font-size: 14px;
        line-height: 1;
        color: #969696;
        white-space: nowrap;
      }
    }
  }

  @include media('>md') {
    &--personal {
      .navbar-tabs {
        margin: 24/16 + rem 0 1rem;
      }
    }
  }
}



@include media('<md') {
  .navbar-tabs-collapse {
    margin: 0 -15px;
  }
  .navbar-tabs {
    box-shadow: none;
    flex-direction: column;
    border: none;
    margin-bottom: 0;

    .nav-item {
      margin: 0;

      &:last-child .nav-link {
        border-color: transparent;
      }
    }

    .nav-link {
      min-height: 60px;
      font-size: 18px;
      border-width: 0 0 1px 0;
      box-shadow: inset 0 -1px 0 0 transparent;

      &.active {
        box-shadow: inset 0 -1px 0 0 $brand-warning;
        border-color: $brand-warning !important;
      }

      &__badge {
        display: none;
      }
    }
  }
}

.panel-service {
  overflow: hidden;
  text-align: center;

  .nav-item {
    flex-grow: 1;
    margin-left: -2px;
  }

  .nav-link {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    line-height: 1.2;
    height: 70px;
    padding: 10px;
    background-color: #f2f2f2;
    border-left: 2px solid #d9d9d9;
    transition: 120ms;

    &:hover {
      background-color: #d9d9d9;
    }

    &__icon {
      width: 1rem;
      margin-right: 10/16 + rem;
    }
  }

  @include media('<md') {
    flex-direction: column;
    border: 1px solid #d9d9d9;
    border-width: 1px 0;
    margin: 0 -15px;

    .nav-item {
      margin-left: 0;
    }

    .nav-link {
      font-size: 18px;
      padding: 8/16 + rem;
      height: 50px;
      border-left: none;
      border: 1px solid #d9d9d9;
      border-width: 1px 0;
    }
  }
}

.main-product {
  margin-bottom: 24/16 + rem;

  &__section-title {
    margin-top: 0;
    margin-bottom: 30/16 + rem;
  }

  &__total {
    flex-grow: 0;
    min-width: 260px;
  }
}

$thumbs-gray-color: #e6e6e6;

.product-gallery {
  &__slider {
    height: 400px;
    margin-bottom: 10px;
  }

  &__slide {
    position: relative;
    height: 100%;
    overflow: hidden;

    .xzoom-gallery {
      position: absolute;
      object-fit: cover;
      /* support for plugin https://github.com/bfred-it/object-fit-images */
      font-family: 'object-fit: cover;';
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
    }

    .image-preloader {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 100;
      transition: z-index 600ms ease;

      .xzoom-loading {
        display: none;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: url(../images/xloading.gif);
      }
    }

    &.swiper-slide-active {
      .image-preloader {
        z-index: -1;
      }
    }
    .xzoom-gallery {
      width: 100% !important;
      margin: 0;
      border: none;
    }

    .video-contain {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 10;

      iframe {
        width: 100%;
        height: 100%;
      }
    }
  }

  &__thumbs {
    padding-left: 21px;
    position: relative;
  }

  &__thumbs-slide {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 54px;
    width: 84px !important;
    background: url() no-repeat center / cover;
    border: 2px solid $thumbs-gray-color;
    transition: 120ms ease;
    text-align: center;

    &.is-active {
      border-color: $brand-primary;
    }
  }

  &__thumbs-arrow {
    display: flex;
    align-items: center;
    position: absolute;
    top: 0;
    z-index: 10;
    height: 100%;
    padding: 3px;
    font-size: 16px;
    background-color: $thumbs-gray-color;
    color: darken($thumbs-gray-color, 12%);
    text-decoration: none !important;

    &.prev {
      padding-right: 7px;
      left: 0;
    }

    &.next {
      padding-left: 7px;
      right: -1px;

      .icon {
        transform: scaleX(-1);
      }
    }
  }

  @include media('>md') {
    .swiper-pagination {
      opacity: 0;
      z-index: -2;
    }
  }

  @include media('<md') {
    &__slider {
      height: 370/768*100vw;
      padding-bottom: 30px;
      margin: 0 -15px;
    }
  }
}

.product-property {
  font-size: 14px;
  color: #444444;

  > * {
    margin-bottom: 15/14 + em;

    &.primary {
      display: flex !important;
    }
  }

  a.under {
    margin-right: 5/16 + rem;
  }

  .widget-rating {
    margin-top: 2px;
    padding: 0;
  }

  &__title {
    font-weight: 700;
  }

  &__color {
    margin: 0 -10px -10px 0;

    .pallete-icon {
      width: 26px;
      height: 26px;
      margin: 0 10px 10px 0;
    }
  }

  &__more {
    float: left;
    margin-top: -12px;
  }

  &__more-btn {
    &.is-active {
      &:before {
        display: inline-block;
        content: 'Показать меньше'
      }
      .text {
        display: none !important;
      }
    }
  }
}

.product-total {
  font-size: 14px;

  &__cost {
    margin: 0.4rem 0;
    font-size: 36/16 + rem;
    line-height: 1.2;
    color: darken($gray-dark, 1%);

    .bold {
      white-space: nowrap;
    }
  }

  &__props {
    margin-bottom: 10px;

    .custom-control {
      font-size: inherit;
    }

    .custom-control-description {
      padding-top: 4/16 + rem;
      border-bottom: 1px dashed;
      color: $brand-primary;
      line-height: 1;
      text-decoration: none !important;
      outline: none;
    }
  }

  &__buy {
    margin-bottom: 15px;
  }

  &__bonus {
    margin-bottom: 15px;
  }

  &__bottom {
    > * {
      margin: 2px 2px 2px 0;
    }

    .bookmark,
    .compare {
      color: #959595;
      transition: 120ms;

      &:hover {
        color: darken(#959595, 14%);
      }
    }

    .icon-heart {
      font-size: 24px;
    }

    .icon-compare {
      font-size: 18px;
    }
  }
}

.product-popup {
  .modal-dialog {
    max-width: 600px;
  }

  .modal-title {
    font-size: 14px;
    font-weight: 600;
  }

  .modal-header {
    padding: 10px 15px;
  }

  &__item {
    .media-body {
      flex: auto;
    }
  }

  &__item-img {
    a {
      display: block;
    }
  }

  &__props {
    font-size: 14px;
    white-space: nowrap;
    color: #464646;
    white-space: normal;

    > li {
      min-height: 39px;
      padding: 9px 0;
      border-top: 1px solid #e0e0e0;
    }

    .product-property__color {
      margin: 0 0 0 -10px;

      .pallete-icon {
        width: 26px;
        height: 26px;
        margin: 5px;
      }
    }
  }

  @include media('>xs') {
    &__item {
      flex-direction: row !important;
    }

    &__item-img {
      width: 40%;
      margin-right: 15px;
    }
  }
}
