.sidebar {
  width: 270px;
  flex-shrink: 0;
  padding: 1rem 10px;
  background-color: #ffffff;

  @include media('>lg') {
    float: left;
    padding: 0 30px 0 0;
    min-height: 100vh;
  }

  @include media('<lg') {
    position: fixed !important;
    top: 0;
    left: 0;
    transform: translate3d(-270px, 0, 0);
    height: 100%;
    overflow: hidden;
    overflow-y: auto;
    z-index: 1000;
    transition: 200ms;

    body.sidebar-open & {
      transform: translate3d(0, 0, 0);
    }
  }
}

.sidebar-filter {
  padding: 0 20/16 + rem;
  margin-bottom: 30/16 + rem;
  border: 1px solid #d4d4d4;

  .form-group {
    margin-bottom: 15/16 + rem;
  }

  &__input-wrap {
    .form-control {
      margin-bottom: 10/16 + rem;
    }

    &.is-active + * {
      overflow: hidden;
      overflow-y: auto;
    }
  }

  .custom-control {
    margin-bottom: 0;
  }

  &__group--collapse {
    max-height: 250px;
    overflow: hidden;
  }

  &__group-list {
    line-height: (24/16);
  }

  &__section-header {
    display: flex;
    align-items: center;
    padding: 25/16 + rem 0 10/16 + rem;
    margin: 0;

    font-family: $roboto;
    text-transform: uppercase;

    .icon-info {
      margin-left: 7/16 + rem;
    }
  }

  &__inline-inputs {
    display: table;
    border-collapse: collapse;
    margin-bottom: 25/16 + rem;

    > * {
      display: table-cell;
      border: 1px solid $gray-lighter;
    }

    .form-control {
      border: none;

      &:focus {
        position: relative;
        box-shadow: 0 0 0 1px inset $brand-primary;
        z-index: 1;
      }
    }
  }

  &__inline-group {
    > * {
      margin-bottom: 0;
    }

    .custom-control {
      margin-right: 5px;
    }
  }

  &__btn-subject {
    line-height: 1;
    border-radius: 2px;
    padding: 8px 13px;
    text-transform: uppercase;
  }

  &__btn-more {
    display: block;
    margin-bottom: 20px;
    font-size: 14/16 + rem;
    line-height: 22/16 + rem;
    color: #9b9b9b;

    &[aria-expanded=true] {
      &:before {
        content: 'Свернуть';
      }

      .btn-show__text {
        display: none;
      }
    }
  }

  &__total {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    padding: 5px 10px;
    margin: 0 -20px;
    background-color: #f1f1f1;
    font-size: 14px;
    line-height: 22/16 + rem;
  }
}
