.sortbar {
  margin: 0 -8/16 + rem 20/16 + rem;

  > * {
    padding: 8/16 + rem;
    margin: -8/16 + rem;

    > li {
      margin: 0 8/16 + rem;
    }
  }

  &__filter {
    flex-grow: 1;

    .custom-control {
      margin: 0;
      font-size: 14/16 + rem;
    }
  }

  &__sort-link {
    font-size: 13px;
    line-height: 18/13 + em;
    font-family: $roboto;
    text-decoration: none !important;
    white-space: nowrap;

    &:after {
      display: none;
      content: '';
      width: 5px;
      height: 9px;
      margin-left: 5px;
      line-height: 1;
      background: url('../images/sort.png') no-repeat center;
    }

    &.is-active {
      &:after {
        display: inline-block
      }
      color: #1d1d1d;
    }

    &.asc {
      &:after {
        transform: scaleY(-1);
      }
    }
  }

  &__switch {
    width: 80px;
    flex-shrink: 0;
  }

  &__switch-link {
    display: inline-block;
    line-height: 1;
    opacity: 0.6;
    color: #4b4b4b !important;
    text-decoration: none !important;

    &.is-active {
      opacity: 1;
    }
  }
}


.mobile-sort {
  margin: 0 -15px 10px;
  overflow: hidden;

  &__list {
    margin-right: -1px;
  }

  &__item {
    flex-grow: 1;
    border-right: 1px solid #c6c6c6
  }

  &__btn {
    display: block;
    width: 100%;
    padding: 14/16 + rem;
    color: $gray-dark;
    background-color: #ededed;
    font-size: 12px;
    text-align: center;
    text-decoration: none !important;
    white-space: nowrap;

    .icon {
      font-size: 15px;
    }

    &:hover {
      color: $gray-dark;
      background-color: lighten(#ededed, 2%);
    }

    &:focus,
    &.is-active {
      color: #c0c0c0;
    }
  }

  &__btn-text {
    margin-left: 5px;
  }

  @include media('<xs') {
    &__btn-text {
      display: none;
    }
  }
}
