$b-feature-item-bg: #f7f2e6;
$b-feature-item-border-color: #d8d4c9;

.b-features {
  &__list {
    display: table;
    table-layout: fixed;
    width: 100%;
    height: 145px;
    margin-left: -1px;
  }

  &__item {
    display: table-cell;
    vertical-align: bottom;
    padding: 10px 10px 20px;
    border-left: 1px solid $b-feature-item-border-color;
    background-color: $b-feature-item-bg;
  }

  &__object {
    margin-bottom: 10px;
  }

  &__link {
    display: inline-block;
    font-size: 16px;
    line-height: 18/16 + em;

    > * {
      display: block;
    }
  }

  @include media('<md') {
    margin: 0 -15px;
    padding-bottom: 30px;

    &__item {
      border-left: none;
    }
  }
}
