$pagination-bg: $bg-light;
$pagination-link-color: #6b6b6b;

.navbar-pagination {
  padding: 10px;
  background-color: $pagination-bg;
}

.pagination {
  &__item {
    margin: 2px;

    &:first-child {
      .icon {
        transform: scaleX(-1);
      }
    }
  }

  &__link {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    height: 30px;
    min-width: 30px;
    padding: 4px;
    margin: 2px;
    font-size: 14px;
    transition: 120ms;

    .icon {
      font-size: 16px;
      color: $pagination-link-color;
    }

    &:hover {

    }
    &:focus {
      background-color: $pagination-bg;
      border-color: $pagination-bg;
      color: $pagination-link-color;
    }
  }
}

.pager {
  margin: 2px -7px;

  > * {
    margin: 2px 7px;
  }

  &__label {
    font-size: 13px;
    font-family: $roboto;
    color: $gray-dark;
  }

  &__input {
    width: 44px;
    height: 30px;
    padding: 8px 7px;
    line-height: 1;
    font-size: 13px;
    font-family: $roboto;
    background-color: #fff;
    border: 1px solid #9f9f9f;
    border-radius: 0;
    box-shadow: none;
    text-align: center;
  }
}

@include media('<md') {

  .navbar-pagination {
    padding: 0;
    background-color: transparent;

    &__nav {
      width: 100%;
    }
  }

  .pagination {
    width: 100%;
    margin-bottom: 20px;

    &__item {
      display: none;
      width: 50%;
      margin: 0;

      &:first-child,
      &:last-child {
        display: block;
      }

      &:first-child {
        margin-right: 14px;

        .pagination__link {
          text-align: left;

          &:after {
            content: 'Назад';
            margin-left: 10px;
          }
        }
      }

      &:last-child {
        .pagination__link {
          text-align: right;

          &:before {
            content: 'Вперед';
            margin-right: 10px;
          }
        }
      }
    }

    &__link {
      display: block;
      width: 100%;
      height: auto;
      padding: 13px;
      border-radius: 6px;
      font-weight: 400;
      font-size: 16px;
      color: $gray-dark;
      background-color: $gray-lighter-2;
      border-color: #bcbcbc;

      .icon {
        &:before {
          content: '\E00C';
        }
      }
    }
  }

  .pager {
    justify-content: center;
    width: 100%;
    margin: 0;
    padding: 1rem;
    border-radius: 4px;
    background-color: $pagination-bg;
    white-space: nowrap;
  }
}
