@mixin text-over() {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

// hidden text full
//
@mixin hide-text() {
  font: 0/0 a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
}

@mixin text-hide() {
  @include hide-text;
}

@mixin posFixed($width, $height) {
  position: absolute;
  left: 50%;
  top: 50%;
  height: $height;
  width: $width;
  margin-left: -$width/2;
  margin-top: -$height/2;
}

// clearfix
@mixin cf() {
  &:before,
  &:after {
    content: " "; // 1
    display: table; // 2
  }
  &:after {
    clear: both;
  }
}
