
.partners {
  &__item {
    height: 110px;
  }

  &__item-link {
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    padding: 10px;
    box-shadow: 0 0 8px rgba($gray-base, 0.19);
    transition: 120ms;
    overflow: hidden;

    &:active {
      box-shadow: 0 0 1px rgba($gray-base, 0.38);
      transition: 60ms ease-in;
    }
  }

  @include media('<md') {
    padding: 0 10px;

    .swiper-button-next {right: -6px}
    .swiper-button-prev {left: -6px}
  }
}
