.form-account {
  margin-bottom: 1rem;

  &__title {
    font-size: 22/16 + rem;
    margin: 0 0 20/16 + rme;
  }

  .form-group {
    margin-bottom: 1rem;

    &.captcha {
      margin: 24/16 + rem 0;
    }
  }

  .text-label {
    margin-bottom: 10/16 + rem;
  }

  .form-control {
    padding: 10/16 + rem;
    font-size: 18/16 + rem;
  }

  &__submit {
    margin: 20/16 + rem 0;
    padding: 10/16 + rem;
  }

  &__helper {
    > * {
      margin-right: 10px;
    }
  }
}
