.main-slider {
  &__slide {
    font-size: 18px;
    background: $brand-primary url() no-repeat center / cover;
    color: $white;
    text-align: center;
    overflow: hidden;
  }

  &__slide-content-wrapper {
    display: flex;
    flex-direction: column;
    padding: 40px 10px 30px;
    height: 440px;
    width: 100%;
    z-index: 2;
    background-image: linear-gradient(to bottom, rgba(#000000, 0.22) 50%, transparent 100%);
  }

  &__slide-text {
    flex-grow: 1;
  }

  &__slide-title {
    margin-bottom: 12px;
    font-weight: 300;
    font-size: 38px;
    line-height: 1;
    color: inherit;
  }

  &__slide-link {
    color: inherit !important;
  }

  &__slide-sale {
    color: inherit;
  }

  &__slide--wide {
    text-align: left;

    .main-slider__slide-content-wrapper {
      padding: 80px 30px 25px;
    }
  }

  &__slide--theme-reverse {
    color: $gray-dark;
  }

  .swiper-pagination {
    bottom: 0;
    margin-bottom: 25px;
  }

  @include media('>xl') {
    &__slide {
      width: 260/1030*100% !important;
    }

    &__slide--wide {
      width: 490/1030*100% !important;
    }
  }

  @include media('<md') {
    margin: 0 -15px;

    &__slide {
      width: 100%;
      height: 300px;
    }
  }
}
