$breadcrumb-bg: #e3e3e3;

.breadcrumbs-wrap {
  margin-bottom: 30px;
}

.breadcrumb-nav {
  .toggle-menu {
    flex-shrink: 0;
    width: 54px;
    height: 54px;
    justify-content: center;
    align-items: center;
    margin-right: 25px;
    background-color: $breadcrumb-bg;
  }
}

.breadcrumb {
  align-items: center;
  padding: 0.5rem;
  margin: 0;
  margin-left: -1rem;
  font-size: 13px;
  background-color: transparent;
}

.breadcrumb-item {
  padding-left: 0.5rem;

  &:before {
    padding: 0 !important;
    margin-right: 0.5rem;
  }
}
