/*************************************
    = Header
                                    */
.page-header {
  padding: 15px 0;
  background-color: $white;
  box-shadow: 0 0 5px rgba($gray-base, 0.17);

  &__navbar.show {
    transition: background-color 120ms;
    background-color: #eeeeee;
  }

  @include media('>md') {
    &__row {
      margin: 0 -10px;

      > * {
        padding: 0 10px;
      }
    }
  }

  &__logo {
    flex-grow: 1;
  }

  &__logo-image {
    margin-right: 15px;
  }

  &__logo-slogan {
    line-height: 18px;
    font-weight: 300;
  }

  &__toggle-menu {
    margin-left: 10px;
    z-index: 1001;
  }

  &__search {
    margin: 10px 0;
  }

  &__location-toggle {
    font-size: 13px;
    color: $gray;

    &:hover {
      color: $gray;
    }
  }

  &__contacts {
    margin: 5px 0;
    line-height: 1;
    font-family: $roboto;
    white-space: nowrap;
  }

  &__number {
    font-size: 18px;
    font-weight: 700;
    color: $gray-dark;
  }

  &__mode {
    font-size: 10px;
    text-transform: uppercase;
    color: $gray-lighter;
  }

  @include media('<md') {
    padding: 0;
    box-shadow: none;

    &__navbar {
      padding: 10px 15px;
    }

    &__contacts-row {
      margin-bottom: 10px;

      > * {
        width: 100%;
      }
    }

    &__contacts {
      margin: 0;
      text-align: center;
    }

    &__number {
      font-size: 26px;
      line-height: 1.4;
    }

    &__mode {
      font-size: 14px;
      line-height: 1;
    }

    .header-location {
      text-align: center;

      &__toggle {
        font-size: 20px;
      }
    }
  }
}

.header-search {
  position: relative;
  display: flex;

  &__input {
    border-color: $gray-lighter;
    border-radius: 0;
  }

  &__subject {
    flex-shrink: 0;
    width: 40px;
    height: 40px;
    padding: 2px;
    margin-left: -1px;
    color: #fff;
    border-radius: 0;

    .icon-search {
      font-size: 19px;
    }
  }
}

.header-location {
  margin-right: 15px;

  &__toggle {
    text-decoration: none !important;
    white-space: nowrap;
    color: $gray;
    border: none;
    outline: none;
    border-bottom: 1px dashed;
    padding: 0;
    margin: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    &:focus,
    &:hover {
      color: $gray;
    }
  }

  &__toggle-text {

    transition: 120ms;

    &:hover {
      border-bottom-color: transparent;
    }
  }

  &__toggle-caret {
    margin-left: 5px;
  }

  &__list {
    padding-top: 0;
    padding-bottom: 0;
    font-size: 14px;
    color: #000101;
    overflow: hidden;

    > li {
      cursor: pointer;
    }
  }
}

.toggle-menu {
  display: block;
  padding: 2px 4px;

  &__bar {
    display: block;
    position: relative;
    perspective: 100px;
    transform-origin: center;
    transform-style: preserve-3d;
    height: 2px;
    width: 17px;
    margin: 4px auto;
    background-color: $gray-lighten;
    transition: 120ms ease;
  }

  &[aria-expanded=true] {
    .toggle-menu__bar {
      &:nth-child(1) {top: 5px; transform: rotate3d(0, 0, 1, 45deg)}
      &:nth-child(2) {transform: rotate3d(0, 1, 0, 90deg)}
      &:nth-child(3) {top: -7px; transform: rotate3d(0, 0, 1, 135deg)}
    }
  }

  &--secondary {
    padding: 5px 7px 4px;
    border: 1px solid $gray-lighten;
    border-radius: 4px;
  }
}

.panel-user {
  display: table !important;

  &__list {
    position: relative;
    display: table-row !important;
    width: 100%;
    height: 50px;
    padding: 0;
    text-align: center;
    background-color: #ffffff;
  }

  &__item {
    display: table-cell !important;
    vertical-align: middle;
    border: 1px solid #cacaca;
    border-width: 1px 0 1px 1px;

    &:first-child { border-left: none; }

    @include media('>xl') {
      border: none;
    }
  }

  &__regist {
    text-align: left;
    padding: 4px;
  }

  &__regist-link {
    position: relative;
    line-height: 22px;
    margin-right: 10px;

    .badge {
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(100%, -50%);
    }
  }

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 4px;
    color: $gray-lighten;
    text-decoration: none !important;

    &:focus {
      color: $gray-lighten;
    }

    &:hover {
      color: $orange;
    }

    .icon {
      line-height: 1;
    }

    .icon-aligned {
      font-size: 17px;
    }
    .icon-heart {
      font-size: 23px;
    }
    .icon-cart {
      font-size: 24px;
    }
  }

  &__count {
    margin-left: 7px;
    color: $gray;
    line-height: 1;
    border-bottom: 1px solid;
    font-size: 13px;
  }

  @include media('<xl') {
    margin-top: 15px;
    table-layout: fixed;

    &__regist {
      > * {
        margin-right: 5px;
      }
    }
  }

  @include media('<md') {
    padding: 0;

    &__list {
      height: 60px;
    }

    &__regist {
      padding-left: 10px;
    }

    &__regist-link {
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }
  }

  @include media('<sm') {
    table-layout: inherit;
  }
}

