.payment-label {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  max-width: 300px;
  min-height: 160px;
  padding: 1rem 1rem 30/16 + rem;
  margin: 0 0 24/16 + rem;
  font-size: 14px;
  line-height: 1.3;
  border: 2px solid #dddddd;
  border-radius: 3px;
  background-color: #ffffff;

  &__desc {
    min-height: 36px;
    margin: 0;
    color: #1b1b1b;
  }

  &__control {
    display: inline-flex;
    align-items: center;
    width: 100%;
    min-height: 60px;
    padding: 0;
    margin: 0;
    font-size: inherit;
    line-height: inherit;
    font-weight: 700;
    color: #1b1b1b;
    position: relative;

    > * {
      display: inline-block;
      margin-right: 8px;
      vertical-align: middle;
    }

    .custom-control-indicator {
      position: static;
      background: none !important;
      width: auto;
      height: auto;

      &:before {
        content: '';
        display: block;
        @include sprite($offcheck);
      }
    }

    .custom-control-input:checked ~ .custom-control-indicator {
      &:before {
        @include sprite($check);
      }
    }
  }
}

.social-share {
  &__title {
    font-size: 20px;
    margin-bottom: 34/16 + rem;
  }

  &__list {
    display: flex;
    justify-content: center;
    text-align: center;
    margin: -10px -10px 0;

    > li {
      display: inline-block;
      margin: 10px;
    }

    > li > a {
      display: table;
      width: 36px;
      height: 36px;
      background-color: #737373;
      text-decoration: none !important;
      transition: 120ms;

      &:hover {
        background-color: lighten(#737373, 7%);
      }

      i {
        display: table-cell;
        vertical-align: middle;
      }
    }
  }
}
