.intro-catalog {
  display: flex;
  margin: -30px 0 20px;

  > * {
    background-color: #f4f4f4;
  }

  &__logo {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 250/1290*100%;
    padding: 25px;

    img {
      flex-shrink: 0;
    }
  }

  &__slider {
    min-height: 230px;
    max-height: 390px;
    flex-grow: 1;

    .swiper-slide {
      background: url() no-repeat center / cover;
    }
  }

  @include media('<md') {
    order: 2;
    flex-wrap: wrap;
    width: auto;
    margin: 0 -15px;

    &__logo {
      width: 100%;

      img {
        max-width: 90px;
      }
    }
  }
}
