.item-list {
  flex-basis: auto;
  max-width: none;

  .product {
    display: block;

    > *:not(.btn-bookmark-rounded) {
      display: table-cell;
      vertical-align: middle;
    }

    &__img {
      height: 100px;
      min-width: 120px;
    }

    &__description {
      width: 100%;
      border-bottom: 1px solid #e0e0e0;

      .divider-horizontal {
        display: none;
      }
    }

    &__props {
      display: block !important;
    }

    &__props-item {
      &.pointer {
        display: none;
      }
    }

    &__bottom {
      padding-top: 10px;
    }

    @include media('>sm') {
      display: table;
      height: 190px;

      > *:not(.btn-bookmark-rounded) {
        vertical-align: top;
        height: 100%;
      }

      &__bookmark-rounded {
        top: 18/16 + rem;
        right: 18/16 + rem;
        width: auto;
        height: auto;
        font-size: 24/16 + rem;

        background-color: transparent !important;
        box-shadow: none !important;
      }

      &__img {
        width: 240/1020*100%;
        min-width: 180px;
      }

      &__description {
        width: 520/1020*100%;
        border-right: 1px solid #e0e0e0;
        border-bottom: none;

        .divider-horizontal {
          display: block;
          margin-right: -10px;
        }
      }

      &__bottom {
        flex-direction: column;
        min-width: 150px;
        padding-top: 60/16 + rem;
        text-align: center;

        > * {
          display: block;
          width: 100%;
        }
      }

      &__rating {
        margin: 0 auto 15px;
      }

      &__btn-compare {
        position: absolute;
        top: 18/16 + rem;
        right: 60/16 + rem;
        height: auto;
        width: auto;
        margin: 0;
        font-size: 18px;
        line-height: 1;
        background-color: transparent;
      }

      &__btn-cart {
        width: 100%;
        margin: 0;
        margin-top: 1rem;
        .btn-cart__text {
          display: inline-block !important;
        }
      }
    }

    @include media('>md') {
      &__description,
      &__bottom {
        padding: 18/16 + rem;
      }

      &__description {
        .divider-horizontal {
          margin-right: -18/16 + rem;
        }
      }

      &__bottom {
        min-width: 220px;
        text-align: left;
      }

      &__rating {
        margin-left: 0;
      }

      &__cost {
        margin: 12/16 + rem 0;
        font-weight: 400;
        font-size: 25/16 + rem;

        .bold {
          font-size: 30/16 + rem;
        }
      }
    }
  }
}
