.cart-content {
  margin-bottom: 50px;

  &__order {

    .divider-horizontal {
      margin: 24/16 +rem 0 40/16 + rem;
      border-top: 2px solid #939393;

      .cart-checklist & {
        & {
          margin-bottom: 24/16 + rem;
        }
      }
    }
  }

  &__address {
    color: #141414;
    line-height: 32px;
  }

  &__order-success {
    font-size: 1rem;
    line-height: 32/16 + rem;

    .title {
      font-size: 22/16 + rem;
    }
  }

  &__total {
    margin-bottom: 45/16 + rem;
    font-size: 18px;
    color: #141414;

    > * {
      &:first-child {
        padding-right: 26/16 + rem;
        font-weight: 600;
      }

      &:last-child {
        font-size: 26/16 + rem;
        white-space: nowrap;
      }
    }

    .cart-checklist & {
      & {
        margin-bottom: 24/16 + rem;
      }
    }
  }

  &__payment {
    margin-bottom: 35px;
  }

  &__btn-submit {
    padding: 9px 28/1290*100%;
    font-weight: 500;
    font-size: 14px;
    text-transform: uppercase;
  }
}

.cart-item {
  &__bottom-divider {
    display: block;
    content: '';
    width: 100%;
    border-bottom: 2px solid #939393;
    margin: 80/16 + rem 0 64/16 + rem;

    .cart-checklist & {
      margin: 24/16 + rem 0;
    }
  }

  &:last-of-type() {
    &__bottom-divider {
      margin-bottom: 20/16 + rem;
    }
  }

  &__list-headings {
    margin: 0;
    font-size: 14px;
    color: #535353;

    + .divider-horizontal {
      margin: 18px 0 28px;
      border-color: 1px solid ;
    }
  }

  &__list-row {
    margin: 0;
    margin-bottom: 30/16 + rem;
  }

  &__list-item {
    padding: 0;
    margin-bottom: 10px;
  }

  &__list-headings,
  &__list-item {
    > .col {
      padding: 0;
      flex-basis: auto;
      display: flex;
    }

    @include media('>md') {
      flex-wrap: nowrap;

      > .col:not(:last-child) {
        padding-right: 10px;
      }

      > .col {
        &:nth-child(1) {
          width: 640/1310*100%;
        }
        &:nth-child(2) {
          width: 180/1310*100%;
          min-width: 130px;
        }
        &:nth-child(3) {
          width: 170/1310*100%;
        }
        &:nth-child(4) {
          width: 140/1310*100%;
        }
        &:nth-child(5) {
          width: 125/1310*100%;
        }
        &:nth-child(n+2) {
          align-items: center;
        }
        &:nth-child(n+3):not(:last-child) {
          min-width: 80px;
        }
        &:last-child {
          width: 60px;
          min-width: 60px;
          vertical-align: middle;
        }
      }
    }
  }

  @include media('<md') {
    &__list-item {
      margin-bottom: 20px;
    }

    &__bottom-divider {
      margin: 40/16 + rem 0 24/16 + rem;
    }
  }
}

.cart-product {
  position: relative;
  background-color: #ffffff;
  color: #000000;
  box-shadow: 0 0 8px 0 rgba(#000101, 0.19);

  &__figure {
    position: relative;
    width: 28%;
    min-width: 100px;
    min-height: 125px;
    padding-right: 10px;
    overflow: hidden;
  }

  &__figure-link {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    right: 10px;
    height: 100%;

    .img-fluid {
      flex-shrink: 0;
      flex-grow: 0;
    }
  }

  &__desc {
    flex-direction: column;
    justify-content: center;
    max-width: 300px;
    width: 100%;
  }

  &__title {
    line-height: 1.1;
  }

  &__btn-remove {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    padding: 10px;
    color: #c46161;
    opacity: 1;
    transition: 120ms ease;
    z-index: 1;

    &:active {
      opacity: 0.60;
    }
  }

  .cart-qty {
    display: inline-flex;
    height: 20px;
    align-items: center;
    white-space: nowrap;
    font-family: 'Arial', sans-serif;
    font-size: 14px;

    &__btn {
      flex-shrink: 0;
      flex-grow: 0;
      width: 20px;
      height: 100%;
      margin: 0 2px;
      border-radius: 2px;
      background-color: #e2e2e2;
      border: none;
      text-align: center;

      &:active {
        background-color: darken(#e2e2e2, 7%) !important;
      }
    }

    &__input {
      width: 34px;
      height: 100%;
      line-height: 1;
      background-color: #fff;
      border: 1px solid #9a9a9a;
      border-radius: 2px;
      text-align: center;
    }

    &__prefix {
      margin-left: 8px;
    }

    @include media('<md') {
      font-size: 16px;
      height: 28px;
      line-height: 20px;

      &__btn {
        width: 28px;
      }

      &__input {
        width: 50px;
      }
    }
  }

  @include media('<md') {
    flex-basis: auto;
    padding: 10px;
    margin: 0 20px 20px 0;
    width: calc(50% - 10px);

    &:nth-child(2n) {
      margin-right: 0;
    }

    @include media('<sm') {
      width: 100%;
      flex-basis: 100%;
      margin-right: 0;
    }

    &__media {
      flex-direction: column;

      > * {
        flex-basis: auto;
        width: auto;
      }
    }

    &__figure {
      padding: 0;
      margin: -10px -10px 10px;

      > * {
        right: 0;
      }
    }

    &__remove {
      position: static;
    }

    &__btn-remove {
      left: auto;
      right: 0;
      height: auto;
      width: auto;
    }

    > .col {
      &:not(.cart-product__media):not(.cart-product__remove) {
        display: flex;
        flex-wrap: wrap;
        line-height: 1;
        margin-top: 10px;

        &:after {
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          color: #ccc;
          border-bottom: 1px dotted;
        }

        > * {
          position: relative;
          background-color: #fff;
          z-index: 1;
        }

        .right {
          margin-left: auto;
        }
      }
    }
  }
}

.cart-item {
  &__promo-code {
    margin: 0 -10px 37px auto;

    > * {
      margin: 0 10px 10px 0;
    }

    .form-control {
      width: auto;
      max-width: 160px;
      padding: 4px 12px;
      border-radius: 3px;
      border-color: #c2c2c2;
    }
  }

  &__total {
    display: table;
    margin: 30px 0 0;
    font-weight: 600;

    > * {
      display: table-row;

      > * {
        display: table-cell;
        vertical-align: middle;
        padding-bottom: 10px;
        line-height: 20px;

        &:first-child {
          padding-right: 1rem;
          font-weight: 400;
          text-align: right;
        }
      }
    }

    .cart-checklist & {
      margin-top: 0;
    }
  }

  &__btn-submit {
    margin-top: 34px;
    width: 100%;
    max-width: 290px;
    margin-left: auto;
    font-size: 14px;
    text-transform: uppercase;
    text-align: center;
  }

  @include media('<md') {
    &__promo-code {
      .form-control {
        flex-grow: 1;
        max-width: none;
      }
    }
  }

  @include media('<sm') {
    &__promo-code {
      text-align: center;

      > * {
        flex-grow: 1;
        width: 100%;
      }
    }

    &__submit-btn {
      width: 100%;
    }
  }
}
