/**************************************
      = Main
                                     */
@include media('>xl') {
  .col-xl-2-4 {
    flex: 0 0 20%;
    max-width: 20%;
  }
}

.main-content {
  &__row-features {
    margin-bottom: 50px;
  }

  &__row-partners {
    margin-bottom: 60px;
  }
}

.main-section {
  &.swiper-container {
    padding: 0 2px;
    margin: 0 -15px;
  }
}

.items-row {
  margin-left: -$grid-gutter;
  margin-right: -$grid-gutter;

  &__item {
    display: flex;
    height: auto !important;
    padding: 5px 10px 40px;
  }

  &__partner {
    padding: 10px;

    @include media('>sm') {
      max-width: 20%;
      flex-basis: 20%;
    }

    @include media('>xl') {
      max-width: 10%;
      flex-basis: 10%;
    }
  }

  &--mod {
    > * {
      padding-bottom: 30px;
    }
  }

  &--related {
    > * {
      padding-bottom: 0;
    }
  }

  @include media('<md') {
    &.swiper-wrapper {
      flex-wrap: nowrap;
      margin: 0;

      .swiper-container-multirow & {
        flex-wrap: wrap;
      }

      &:not(.partners__row) > * {
        padding: 4px 2px 30px;
      }
    }
  }
}

.panel-show {
  margin: 30/16 + rem 0;

  &__label {
    margin: 0 25px 0 0;
    font-size: 18px;
    line-height: 22/18 + em;
    font-weight: 600;
    color: $gray-dark;
  }

  &__btn.btn {
    padding: 9px 10px;
    border-radius: 4px;
    font-weight: 300;
    font-family: inherit;
    font-size: 14px;
    line-height: 1;
    text-transform: none;
  }

  @include media('<md') {
    margin: 10px 0 15px;
    justify-content: space-between;

    &__btn.btn {
      padding: 0;
      border: none;
      background-color: transparent !important;
      box-shadow: none;
      outline: none;
      font-family: $font-family-base;
      font-weight: 600;
      color: $teal;
      transition: 120ms;
    }
  }

  .page-content-column & {
    &:nth-child(1) {
      margin-top: 15px;
    }
    &:nth-child(n+2) {
      margin-top: 0;
    }
  }
}

.mobile-navbar-menu {
  padding: 10px;
  background-color: #f4f4f4;
  color: #262626;

  .icon-align-menu {
    margin-right: 10px;
    color: #888888;
    font-size: 14px;
  }
}

@include media('>md') {
  .main-intro {
    margin: 25px 0;
  }

  .main-content {
    background-color: $gray-lightest-ease;
  }

  .page-content {
    margin-bottom: 40px;
  }

  .main-section {
    &.swiper-container {
      width: auto;
      padding: 0;
      margin: 0;
    }

    &.swiper-container,
    > .swiper-container {
      overflow: visible;
    }

    .items-row.swiper-wrapper {
      width: auto;
    }
  }
}

.wrapper-slider-items {
  position: relative;

  > .swiper-container {
    overflow: hidden;
    padding: 0 4px;
  }
}

@include media('>lg') {
  .page-title.offset,
  .page-content-column {
    float: left;
    width: calc(100% - 271px);
  }
}

@include media('<lg') {
  html,
  body {
    &.sidebar-open {
      overflow: hidden;
    }
  }

  .blocked {
    position: fixed !important;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(#000, 0.20);
    z-index: 10;
  }
}

@include media('<md') {
  .row--sm-2 {
    margin-left: -11px;
    margin-right: -11px;

    > * {
      padding-left: 2px;
      padding-right: 2px;
      padding-bottom: 11px;
    }
  }
}
