.container {
  width: 100%;
  max-width: 1320px;
}

.row--10 {
  margin-left: -10px;
  margin-right: -10px;

  > [class*=col] {
    padding-left: 10px;
    padding-right: 10px;
  }
}

button {
  background: none;
  border: none;
  padding: 0;
  outline: none !important;
  box-shadow: none;
  cursor: pointer;
}

.pointer {
  cursor: pointer !important;
}

.badge {
  font-size: 9/16*100%;
  font-weight: 300;
  font-family: $roboto;
  line-height: 1;
  padding: 4/9 + em;
  border-radius: 1em;

  &--red {
    color: #fff;
    background-color: $red-lighten;
  }

  &-primary {
    background-color: $brand-primary;
  }
  &-danger {
    background-color: $red-lighten;
  }
}

.bold {
  font-weight: 700;
}

.dashed {
  border-bottom: 1px dashed;
}

.divider-horizontal {
  margin: 13px 0;
  border-top: 1px solid #e0e0e0;

  &--2x {
    border-top-width: 2px;
  }

  &--2xdark {
    border-top-width: 2px;
    border-color: #c6c6c6;
  }
}

.img-helper {
  opacity: 0;
}

.btn-compare {
  display: inline-block;
  font-size: 18px;
  line-height: 1;
  color: $gray-lighten;
  transition: 120ms;

  &:hover {
    color: #6e6e6e;
  }
}

.btn-cart {
  display: inline-block;
  min-width: 42px;
  padding: 7px;
  border-radius: 3px;
  font-size: 18px;
  line-height: 1;
  color: #fff;
  background-color: $orange;
  transition: 120ms;

  .icon {
    font-size: 24px;
  }

  &__text {
    margin-left: 10/18 + em;
    font-size: 14/16 + rem;
    font-weight: 700;
    line-height: 1;
    vertical-align: middle;
    font-family: $roboto;
    text-transform: uppercase;

    @include media('>md') {
      font-size: 18/16 + rem;
    }
  }

  &:hover {
    background-color: darken($orange, 10%);
  }

  &--wide,
  &.w-100 {
    border: 1px solid darken($orange, 18%);
  }
}

.btn-bookmark-rounded {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 36px;
  height: 36px;
  font-size: 16px;
  line-height: 1;
  border-radius: 50%;
  background-color: rgba(#fff, 0.70);
  text-decoration: none !important;
  color: $gray-lighten;
  box-shadow: 0 0 13px rgba($gray-base, 0);
  transition: 120ms;

  &.active {
    background-color: rgba(#fff, 0.70) !important;
    color: $red-lighten !important;
    box-shadow: 0 0 13px rgba($gray-base, 0) !important;
  }

  &:focus {
    color: $gray-lighten;
  }

  &:hover {
    background-color: #ffffff;
    box-shadow: 0 0 13px rgba($gray-base, 0.54);
    color: $red-lighten;
  }

  .icon {
    display: inline-flex;
  }
}

.swiper-pagination:not(.swiper-pagination-bullets-dynamic) {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.swiper-pagination-bullets-dynamic {
  .swiper-pagination-bullet {
    transform: none;
    background-color: #262626;
    opacity: 0.60;

    &-active {
      opacity: 1;
    }
  }

  .swiper-pagination-bullet-active-prev-prev,
  .swiper-pagination-bullet-active-next-next {
    width: 0;
    height: 0;
    border: 8px solid $gray-dark;
    border-right: none;
    border-width: 4px 8px 4px;
    border-color: transparent $gray-dark transparent;
    border-radius: 0;
    transform: none;
    background-color: transparent;
    opacity: 1;
    transition: 120ms ease;
  }

  .swiper-pagination-bullet-active-prev-prev {
    transform: scaleX(-1);
  }
}

.swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  margin: 4px;
  opacity: 1;
  background: rgba($gray-dark, 0.60);
}

.swiper-pagination-bullet-active {
  opacity: 1;
  background-color: rgba($gray-dark, 1);
}

.slider-wrapper {
  position: relative;
}

.slider-arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  background-color: $brand-primary;
  background-image: none;
  box-shadow: 0 0 14px rgba(#000000, 0);
  opacity: 0.70;
  transition: 120ms;

  &.swiper-button-disabled {
    opacity: 0.70;
  }
  &:hover {
    opacity: 1;
    box-shadow: 0 0 14px rgba(#000000, 0.30);
  }

  .wrapper-slider-items & {
    top: 36%;
    margin: 0;

    &.swiper-button-prev {
      left: -15px;
    }

    &.swiper-button-next {
      right: -15px;
    }
  }
}

@include media('<md') {
  .btn-compare,
  .btn-cart:not(.btn-cart--wide) {
    width: 36px;
    height: 36px;
    line-height: 36px;
    vertical-align: middle;
  }

  .btn-cart:not(.btn-cart--wide) {
    font-size: 21px;
    padding: 0;
    min-width: auto;
  }

  .btn-compare {
    font-size: 13px;
    background-color: #dbdada;
    border-radius: 3px;

    &:hover {
      background-color: lighten(#dbdada, 5%);
    }
  }
}

%pallete-icon {
  display: inline-block;
  vertical-align: middle;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background: #dcdcdc url() no-repeat center;
  background-size: cover;
}

.pallete-icon {
  @extend %pallete-icon;
}

$checkbox-bg: #708043;

.custom-control {
  align-items: center;
  padding-left: 20/16 + rem;
  margin-bottom: 0;
  font-family: $roboto;
  font-weight: 300;
  font-size: 1rem;
  line-height: 23/16 + rem;

  .pallete-icon {
    margin-right: 0.5rem;
  }

  .custom-control-input:checked~.custom-control-indicator {
    background-color: $checkbox-bg;
  }
}

.custom-control-indicator {
  top: 5.5/16 + rem;
  width: 13/16 + rem;
  height: 13/16 + rem;
  background-size: 70% 70%;

  .custom-radio & {
    background-size: 0;
  }

  .custom-checkbox & {
    border-radius: 0.2rem;
  }

  outline: none !important;
  box-shadow: none !important;
}

.btn-show {
  &--dashed {
    border-bottom: 1px dashed;
    text-decoration: none !important;
  }

  &[aria-expanded=true],
  &.is-active {
    border-bottom: none;

    &:before {
      content: 'Свернуть';
    }

    .btn-show__text {
      display: none;
    }
  }
}

.form-control {
  border-radius: 0;
  padding: 0.34rem .75rem;
  line-height: 1.4;
}

.icon-info {
  display: inline-block;
  vertical-align: middle;
  width: 14px;
  height: 14px;
  font-family: $roboto;
  font-size: 10px;  line-height: 1;
  border: 1px solid;
  color: #a7a7a7;
  border-radius: 50%;
  background-color: #ffffff;

  &:before {
    content: '?';
  }
}

.widget-rating {
  padding: 0;
  margin-left: 0 !important;
}

.xzoom-source,
.xzoom-preview,
.xzoom-hidden {
  z-index: 11;
  border: none;
  box-shadow: none;
}

.xzoom-lens {
  border-color: #ffffff;
  box-shadow: none;
  cursor: none;
}

// custom bootstrap modal
.modal-content {
  border-radius: 0;
  outline: 0;
  border: none;
  box-shadow: none;
}

@include media('<lg') {
  .xzoom-source,
  .xzoom-preview {
    display: none !important;
  }
}

.input-qty {
  position: relative;
  width: 64px;
  height: 35px;
  padding-right: 17px;
  font-size: 18px;
  line-height: 1;
  color: $gray-dark-1;
  border: 1px solid #b0b0b0;
  border-radius: 3px;
  background-color: #ffffff;
  overflow: hidden;

  &__input {
    padding: 2px;
    width: 100%;
    height: 100%;
    background: none;
    border: none;
    outline: none;
    box-shadow: none;
    text-align: center;
  }

  &__btn {
    position: absolute;
    top: -1px;
    right: -1px;
    width: 18px;
    height: 18px;
    line-height: 1;
    vertical-align: middle;
    font-weight: 700;
    border: 1px solid #b0b0b0;
    color: #5a5a5a;
    background-color: #e8e8e8;
    z-index: 1;

    i {
      vertical-align: top;
      line-height: 15px;
    }

    &:first-child {
      border-top-right-radius: 3px;
    }

    &:last-child {
      top: auto;
      bottom: -1px;
      border-bottom: none;
      border-bottom-right-radius: 3px;
    }

    &:active {
      background-color: darken(#e8e8e8, 5%);
    }
  }

  @include media('<md') {
    width: 100%;
    height: 26px;
    padding: 0 26px;
    font-size: 16px;
    line-height: 20px;

    &__btn {
      width: 28px;
      height: 26px;

      &:nth-of-type(1) {
        right: auto;
        left: -1px;
      }

      i {
        line-height: 25px;
      }
    }
  }
}

.youtube-play-custom {
  display: inline-block;
  padding: 20px 29px;
  background-color: $btn-youtube-play;
  border-radius: 10px;
  transform: translate(-50%, -50%);
  transition: 120ms ease-out;

  &:before {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -9px 0 0 -5px;
    border: 18px solid #fff;
    border-width: 9px 0 9px 14px;
    border-color: transparent transparent transparent #fff;
  }

  &:hover {
    background-color: lighten($btn-youtube-play, 10%);
  }

  &:active {
    background-color: $btn-youtube-play;
  }

  @include media('<sm') {
    padding: 13px 18px;
    border-radius: 5px;

    &:before {
      border-width: 6px 0 6px 9px;
      margin: -6px 0 0 -3px;
    }
  }
}

.btn-scroll-up {
  position: fixed;
  right: 1rem;
  bottom: 2.7rem;
  width: 3.2rem;
  height: 3.2rem;
  background-color: $yellow;
  border-radius: 6px;
  z-index: 10;
  box-shadow: 0 0 22px rgba(#000, .30);
  opacity: 0;
  visibility: hidden;
  transition: 0.25s linear, visibility 0s 25s;

  &.is-active {
    bottom: 4.5rem;
    opacity: 1;
    visibility: visible;
    transition: 0.25s linear, visibility 0s 0s;
  }

  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 0.8rem;
    height: 0.8rem;
    border: 2px solid #000;
    margin: -0.2rem 0 0 -0.4rem;
    transform: rotate(-45deg);
    border-width: 2px 2px 0 0;
    opacity: 0.50;
  }

  &:hover {
    box-shadow: 0 0 8px rgba(#000, .30);
  }
}
