.intro-features {
  position: relative;
  min-height: 595px;
  padding-top: 74/16 + rem;
  padding-bottom: 30/16 + rem;
  z-index: 1;

  .h1 {
    margin-bottom: 36/16 + rem;
    font-size: 30/16 + rem;
    font-weight: 700;
  }

  .h4 {
    margin-bottom: 20/16 + rem;
    font-size: 18/16 + rem;
    font-weight: 700;
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: 60% 0;
    background-size: cover;
  }

  &__list {
    line-height: (22/16);

    > li {
      margin-bottom: 20/16 + rem;
    }
  }

  @include media('>md') {
    margin-top: -30/16 + rem;
  }
}

.want-to-sell {
  padding: 64/16 + rem 0;

  .h3 {
    margin-bottom: 64/16 + rem;
    font-weight: 600;
    font-size: 30/16 + rem;
    line-height: 1.1;
    text-align: center;
  }

  .features {
    line-height: (22/16);
    text-align: center;

    > * {
      margin-bottom: 64/16 + rem;
    }

    &__img {
      margin-bottom: 35/16 + rem;
    }

    &__title {
      min-height: 48/22 + em;
      margin-bottom: 12/16 + rem;
      font-size: 22/16 + rem;
      line-height: 1.1;
      color: $gray-base;
    }

    p {
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
}

.how-to-buy-content {
  padding-bottom: 13.4vh;
  margin-top: -32/16 + rem;

  .back-to-catalog {
    max-width: 220px;
    width: 100%;
  }

  @include media('<md') {
    padding-bottom: 8.5vh;
  }
}

.sticky-nav {
  top: 0;
  position: sticky;
  padding-top: 44/16 + rem;
  font-size: 14px;

  h5 {
    font-size: inherit;
  }

  li {
    &.is-active {
      .nav-link span {
        border-bottom-color: transparent;
      }
    }
  }

  .nav-link {
    span {
      border-bottom: 1px dotted;
    }

    &:hover {
      opacity: 0.70;
    }
  }
}

.site-details {
  padding-top: 44/16 + rem;

  &__row {
    margin-bottom: 25/16 + rem;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  &:last-of-type {
    margin-bottom: 0;

    .hr-bottom {
      display: none;
    }
  }

  h5 {
    font-size: 13px;
    font-weight: 700;
  }

  &__footer {
    margin-bottom: 55/16 + rem;

    p {
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }

  hr.hr-bottom {
    margin-top: 28/16 + rem;
  }

  &__img {
    position: relative;
    border-radius: 4px;
    background-color: $teal-light;

    .img-scene-zoom {
      position: absolute;
      bottom: 0;
      left: 0;
      max-width: 100%;
      display: none;
      z-index: 1;
    }

    img {
      width: 100%;
      margin-top: -18/16 + rem;
    }

    &.is-active {
      .img-scene-zoom {
        display: block;
      }

      .site-details__toggle {
        .icon {
          display: none;
        }
      }
    }
  }

  &__toggle {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;

    i.icon {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      display: block;
      width: 76/16 + rem;
      height: 76/16 + rem;
      line-height: 76/16 + rem;
      border-radius: 50%;
      background-color: $yellow;
      box-shadow: 0 0 37px rgba(#000, .35);
      transition: 120ms ease;
      margin-top: 0;
      text-align: center;
      z-index: -1;
    }

    &:hover {
      .icon {
        box-shadow: 0 0 17px rgba(#000, .35);
      }
    }

    &:focus:active {
      .icon {
        top: calc(50% - 1px);
        box-shadow: 0 0 0 rgba(#000, 0.35);
      }
    }
  }

  .program-list {
    p {
      margin-bottom: 13/16 + rem;

      &:last-of-type {
        margin-bottom: 0;
      }
    }

    > li {
      margin-bottom: 13/16 + rem;

      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
}

@include media('<md') {
  .want-to-sell {
    padding: 32/16 + rem 0;

    .h3 {
      margin-bottom: 32/16 + rem;
    }

    .features {
      line-height: (22/16);
      text-align: center;

      &__img {
        margin-bottom: 10/16 + rem;
      }

      > * {
        margin-bottom: 32/16 + rem;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  .site-details {
    padding-top: 22/16 + rem;
  }
}

@include media('<sm') {
  .want-to-sell {
    .features {
      line-height: (22/16);
      text-align: center;

      &__img {
        max-width: 24%;
        margin: 0 auto 1rem;
      }

      h4 {
        min-height: auto;
        font-size: 1.2rem;
      }
    }
  }
}
