.site-menu {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 0;
  background-color: #eeeeee;
  color: #525252;
  z-index: 1000;

  &__header {
    padding: 10px 15px;
  }

  &__list {
    border-top: 1px solid #cecece
  }

  &__item {
    display: flex;
  }

  &__link {
    display: flex;
    align-items: center;
    width: 100%;
    min-height: 54px;
    font-size: 14px;
    color: $gray-base;
    border-top: 1px solid #cecece;

    &:focus,
    &:hover {
      color: $gray-base;
    }

    > * {
      display: inline-block;
      padding: 3px;
    }
  }

  &__link-icon {
    width: 70px;
    overflow: hidden;
    text-align: center;

    &:before {
      display: inline-block
    }
  }
}
