/*************************************
    = Footer
                                    */
.page-footer {
  font-size: 14px;
  background-color: #fff;

  .container {
    padding: 15px;

    > .row {
      > * {
        padding: 15px;
      }
    }
  }

  &__menu {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
  }

  &__menu-link {
    display: inline-block;
    padding: 0 15px;
  }

  &__copyright {
    color: #acacac;
  }

  @include media('<md') {
    font-size: 18px;

    &__menu {
      flex-direction: column;
      text-align: center;
      margin: 0 -15px;
    }

    &__menu-link {
      width: 100%;
      padding: 10px;
      border: 2px solid #d5d5d5;
      border-width: 2px 0;
      margin-top: -2px;
    }
  }
}
