$item-bg: #ffffff;

.product {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: $item-bg;
  box-shadow: 0 0 8px rgba(#000101, 0.19);

  &--related {
    box-shadow: none;
  }

  &__flag {
    position: absolute;
    top: 15px;
    left: -4px;
    padding: 10px 20px 10px 10px;
    min-width: 90px;
    min-height: 38px;
    font-size: 18px;
    line-height: 1;
    font-weight: 300;
    background-color: $red;
    font-family: $roboto;
    color: #fff;
    white-space: nowrap;
    z-index: 1;

    &:before {
      content: '';
      position: absolute;
      bottom: -4px;
      left: 0;
      height: 0;
      width: 0;
      border-width: 2px;
      border-color: $red-dark $red-dark transparent transparent ;
      border-style: solid;
    }

    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: -5px;
      height: 0;
      width: 0;
      border-width: 19px 5px;
      border-color: $red transparent $red transparent;
      border-style: solid;
    }
  }

  &__bookmark {
    position: absolute;
    top: 10px;
    right: 10px;
    color: $gray-lighten;
    font-size: 24px;
    line-height: 1;
    text-decoration: none !important;
    z-index: 1;
    transition: 120ms;

    &:focus {
      color: $gray-lighten;
    }

    &:hover {
      color: $red;
    }
  }

  &__bookmark-rounded {
    position: absolute;
    top: 5px;
    right: 5px;
    z-index: 1;
  }

  &__figure {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    height: 310px;
  }

  &__img {
    height: 195px;
    margin: 0;
    overflow: hidden;

    a {
      display: block;
      height: 100%;
      background: $gray-lighten url() no-repeat center / cover;
      transform-origin: center center;
      transform: scale(1);
      transition: 300ms ease;

      &:hover {
        transform: scale(1.2);
      }
    }
  }

  &__description {
    padding: 1rem 10px 12/16 + rem;
    flex-grow: 1;
  }

  &__title {
    margin: 0;
    font-size: 16px;
    font-weight: 400;

    + .product__category {
      margin-bottom: 0;
    }
  }

  &__category {
    margin: 4px 0;
    font-size: 12px;
    line-height: 18px;
    color: #909090;

    a {
      color: inherit;
    }
  }

  &__props {
    flex-grow: 1;
    font-size: 13px;
    line-height: 1;
    font-weight: 400;
    color: #686868;

    > * {
      display: flex;
      align-items: center;
      margin-bottom: 5px;

      &:last-child {
        margin-bottom: 0;
      }

      > * {
        &:first-child {
          margin-right: 4px;
        }
      }
    }

    a {
      color: #161616;
      text-decoration: none !important;
      border-bottom: 1px solid $gray-lighten;

      &:hover {
        border-bottom: 1px solid transparent;
      }
    }
  }

  &__props-related {
    margin: 0 -10px;
    font-size: 14px;
    line-height: 38px;
    white-space: nowrap;
    border-top: 1px solid #e0e0e0;
    color: #464646;

    > * {
      height: 39px;
      padding: 0 14px;
      border-bottom: 1px solid #e0e0e0;
    }

    .widget-rating {
      width: auto !important;
    }

    .cost {
      font-size: 18px;
    }
  }

  &__props-pallete {
    display: inline-block;
    text-align: center;
  }

  &__props-pallete-item {
    @extend %pallete-icon;
    margin: 2px;
  }

  .divider-horizontal {
    + .product__bottom {padding-top: 0;}
  }

  &__bottom {
    flex-wrap: wrap;
    padding: 10/16 + rem;
    padding-top: 0;
    min-height: 30px;

    &.text-center {
      > * {
        width: 100%;
      }
    }

    .text-right {
      margin: 2px 0;

      > * {
        margin: 2px 0 2px 8px;
      }
    }
  }

  &__rating {
    padding: 0;
    margin-bottom: 5px;
  }

  &__cost {
    font-size: 18px;
    font-weight: 700;
    line-height: 1;
    color: #242424;

    .bold {
      display: inline-block;
      vertical-align: bottom;
      font-size: 18px;
      line-height: 1;
      font-weight: 700;
    }
  }

  &__old {
    font-size: 14px;
    line-height: 18px;
    text-decoration: line-through;
    color: #585858;
  }

  &__discount {
    display: inline-block;
    vertical-align: middle;
    min-width: 48px;
    min-height: 38px;
    padding: 10px 5px;
    background-color: $red;
    color: #fff;
    font-size: 18px;
    line-height: 1;
    font-family: $roboto;
  }

  &__stock {
    color: $green;
    font-family: $roboto;
    font-size: 13px;
    white-space: nowrap;

    > * {
      display: inline-flex;
      vertical-align: middle;
      margin-right: 5px;
    }
  }

  @include media('<md') {
    &__figure {
      align-items: flex-end;
      height: 190px;
      padding-bottom: 10px;

      img {
        max-width: 130px;
      }
    }

    &__bottom {
      align-items: center !important;

      .text-right {
        white-space: normal;
        width: 50px;
      }
    }
  }
}

.product-related {
  position: relative;
  padding: 0 10px 25px 0;
  overflow: hidden;

  &__props-headings {
    width: 260/1310*100%;
    flex-basis: auto;
    font-size: 14px;
    font-weight: 800;
    color: #626262;
    line-height: 38px;
    white-space: nowrap;
    border-top: 1px solid #e0e0e0;

    > * {
      height: 39px;
      border-bottom: 1px solid #e0e0e0;
    }
  }

  &__slider {
    position: relative;
    width: calc(100% - 260px);
    flex-basis: auto;

    .wrapper-slider-items {
      overflow: hidden;
    }

    .swiper-wrapper {
      width: auto;
    }

    .swiper-slide {
      padding-bottom: 0 !important;
    }
  }

  .slider-arrow {
    top: 37%;

    &.swiper-button-prev {
      left: -25px;
    }
    &.swiper-button-next {
      right: -25px;
    }
  }

  .swiper-pagination {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }

  @include media('<md') {
    > .container {
      padding: 0;
    }

    &__props-headings {
      position: relative;
      top: -46px;
      width: 40%;

      > * {
        padding: 2px 10px;
      }
    }

    &__slider {
      width: 60%;
    }

    .product {
      &__props-related {
        margin-bottom: 10px;
      }

      &__btn-buy {
        order: 1;
        width: 154px;
      }
    }
  }
}
